.popup{
  /*border: 1px solid #fff;*/
  font-family: 'Inter', Helvetica, sans-serif;
  position: fixed;
  display: flex;
  background: transparent;
  transform: scale(0);
  transition: 0.5s;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
}